/* App.css */

body {
  font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
  margin: 0;
  background-color: #f7f8fa;
  color: #333;
}

.app {
  max-width: 900px;
  margin: 0 auto;
  padding: 20px;
}

.header {
  background-color: #2c3e50;
  padding: 20px;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  font-weight: 700;
  font-size: 28px;
}

.nav a {
  color: white;
  margin-left: 15px;
  text-decoration: none;
  font-weight: 600;
}

.nav a:hover {
  text-decoration: underline;
}

.hero {
  background-color: #2980b9;
  color: white;
  padding: 50px 20px;
  text-align: center;
  border-radius: 10px;
  margin: 30px 0;
}

.btn-primary {
  background-color: #27ae60;
  border: none;
  color: white;
  padding: 12px 25px;
  border-radius: 25px;
  font-size: 16px;
  cursor: pointer;
  font-weight: 600;
  transition: background-color 0.3s ease;
  margin-top: 20px;
}

.btn-primary:hover {
  background-color: #1e8449;
}

.products {
  margin-top: 30px;
}

.section-title {
  font-size: 26px;
  font-weight: 700;
  margin-bottom: 25px;
  text-align: center;
  color: #2c3e50;
}

.product-list {
  display: flex;
  gap: 20px;
  flex-wrap: wrap;
  justify-content: center;
}

.product-card {
  background-color: white;
  padding: 20px;
  border-radius: 12px;
  box-shadow: 0 3px 6px rgb(0 0 0 / 0.1);
  width: 280px;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.product-card label {
  display: flex;
  align-items: center;
  gap: 8px;
  font-size: 18px;
  cursor: pointer;
}

.price {
  font-weight: 700;
  font-size: 18px;
  color: #27ae60;
}

.btn-buy {
  background-color: #2980b9;
  border: none;
  color: white;
  padding: 10px 20px;
  border-radius: 25px;
  cursor: pointer;
  font-weight: 600;
  transition: background-color 0.3s ease;
  align-self: flex-start;
}

.btn-buy:hover {
  background-color: #1c5d8a;
}

.calculator {
  background-color: #ecf0f1;
  margin-top: 40px;
  padding: 20px;
  border-radius: 12px;
  text-align: center;
  box-shadow: 0 3px 6px rgb(0 0 0 / 0.1);
}

.contact {
  margin-top: 50px;
  padding: 20px;
  background-color: #dfe6e9;
  border-radius: 12px;
  text-align: center;
  font-size: 18px;
}

.footer {
  text-align: center;
  padding: 20px;
  margin-top: 50px;
  font-size: 14px;
  color: #999;
}
